<template>
    <v-container fluid class="fill-height login pa-0">
        <ToggleDark class="toggle-dark"/>
        <Logo class="logo-pos"/>
        
        <v-row align="center" justify="center" class="fill-height ma-0">
            <v-col cols="12" sm="8" md="6" lg="4" xl="3">
                <v-card class="login-card elevation-24 px-6 py-8" rounded="xl">
                    <div class="text-center mb-6">
                        <h1 class="text-h4 font-weight-bold primary--text login-title">{{ title }}</h1>
                    </div>

                    <v-form @submit.prevent="handleSubmit">
                        <div v-for="(field, index) in fields" :key="field.key" class="mb-4">
                            <v-text-field
                                v-model="formData[field.key]"
                                :label="field.label"
                                :placeholder="field.placeholder"
                                :type="field.type || 'text'"
                                :append-inner-icon="field.icon === 'Lock' ? 'mdi-lock' : undefined"
                                variant="outlined"
                                color="primary"
                                density="comfortable"
                                :loading="loading"
                                rounded="lg"
                                class="login-input"
                                bg-color="surface"
                            >
                            </v-text-field>
                        </div>

                        <v-btn
                            block
                            color="primary"
                            size="x-large"
                            rounded="lg"
                            elevation="4"
                            class="mt-6 text-capitalize font-weight-bold tracking-wide"
                            :loading="loading"
                            type="submit"
                        >
                            {{ submitText }}
                        </v-btn>
                    </v-form>
                </v-card>
            </v-col>
        </v-row>
        <Footer class="footer"/>
    </v-container>
</template>

<script>
import Footer from '@/components/Footer.vue';
import ToggleDark from '@/components/ToggleDark.vue';
import Logo from '@/components/Logo.vue';
import { mapGetters } from 'vuex';
import backgroundManager from '@/mixins/backgroundManager';

export default {
    name: 'BaseLogin',
    mixins: [backgroundManager],
    props: {
        title: { type: String, required: true },
        fields: { type: Array, required: true },
        submitText: { type: String, default: '登录' },
        backgroundKey: { type: String, required: true },
        isAdmin: { type: Boolean, default: false },
        loading: { type: Boolean, default: false }
    },
    data() {
        return {
            formData: {},
        }
    },
    computed: {
        ...mapGetters(['userConfig']),
    },
    components: {
        Footer,
        ToggleDark,
        Logo
    },
    created() {
        this.initFormData();
    },
    mounted() {
        this.initializeBackground(this.backgroundKey, '.login', !this.isAdmin, true);
    },
    methods: {
        initFormData() {
            const newFormData = {};
            this.fields.forEach(field => {
                newFormData[field.key] = '';
            });
            this.formData = newFormData;
        },
        handleSubmit() {
            if (this.loading) return;
            this.$emit('submit', { ...this.formData });
        }
    }
}
</script>

<style scoped>
.login {
    position: relative;
    background-size: cover;
    background-position: center;
    transition: background 0.5s ease;
}

.login-card {
    background: rgba(var(--v-theme-surface), 0.85) !important;
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.login-title {
    font-family: 'Righteous', sans-serif;
    letter-spacing: 1px;
    background: linear-gradient(45deg, var(--v-theme-primary), var(--v-theme-secondary));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.toggle-dark {
    position: fixed;
    top: 24px;
    right: 24px;
    z-index: 100;
}

.logo-pos {
    position: fixed;
    top: 24px;
    left: 24px;
    z-index: 100;
}

.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
}
</style>
